import Vue from 'vue'
import axios from 'axios'
import globle from '@/api/global_varialble'

Vue.prototype.globle = globle;
axios.defaults.baseURL = globle.baseURL;
axios.defaults.timeout = globle.timeout;


export function getEnterpriseList() {
  return axios({
    url: 'api/user/getAuthEnterprise',
    method: 'get',
  })
}

export function getEnterpriseInfoById(id) {
  return axios({
    url: 'api/user/getEnterAuthInfo?id='+id,
    method: 'get',
  })
}

export function postEnterAuthResult(parameter) {
  return axios({
    url: 'api/user/postEnterAuthResult',
    method: 'post',
    data: parameter
  })
}

export function getAllFarmerGroupsList() {
  return axios({
    url: 'api/user/getAllGroups',
    method: 'get',
  })
}

export function getGroupList (id) {
  return axios({
    url: 'api/user/getOneGroupInfo?id='+id,
    method: 'get',
  })
}

export function postGroupAuthInfo(parameter) {
  return axios({
    url: 'api/user/postGroupAuthInfo',
    method: 'post',
    data: parameter
  })
}

export function getAllNeeds() {
  return axios({
    url: 'api/needs/getAllNeeds',
    method: 'get',
  })
}

export function getAllOrders() {
  return axios({
    url: 'api/order/getNeedOrders',
    method: 'get',
  })
}

export function getAllManagers() {
  return axios({
    url: 'api/user/allManager',
    method: 'get',
  })
}

export function deletManager(parameter) {
  return axios({
    url: 'api/user/deleteManager',
    method: 'post',
    data: parameter
  })
}

export function addManager (parameter) {
  return axios({
    url: 'api/user/registerManager',
    method: 'post',
    data: parameter
  })
}


