import api from './index'
// import { axios } from '@/utils/request'
// import { axios } from '@/network/request'
import Vue from 'vue'
import axios from 'axios'
import globle from '@/api/global_varialble'

Vue.prototype.globle = globle;
axios.defaults.baseURL = globle.baseURL;
axios.defaults.timeout = globle.timeout;

export function login (parameter) {
  return axios({
    url: 'api/user/login',
    method: 'post',
    data: parameter
  })
}

export function register (parameter) {
  return axios({
    url: 'api/user/register',
    method: 'post',
    data: parameter
  })
}

export function recover (parameter) {
  return axios({
    url: 'api/user/changePassword',
    method: 'post',
    data: parameter
  })
}

export function getInfo () {
  return axios({
    url: '/user/info',
    method: 'get',
    headers: {
      'Content-Type': 'application/json;charset=UTF-8'
    }
  })
}

export function getCurrentUserNav (token) {
  return axios({
    url: '/user/nav',
    method: 'get'
  })
}

export function logout () {
  return axios({
    url: '/auth/logout',
    method: 'post',
    headers: {
      'Content-Type': 'application/json;charset=UTF-8'
    }
  })
}

export function postDemandRequest (parameter) {
  return axios({
    url: 'api/needs/postneeds',
    method: 'post',
    data: {parameter,
    'id':localStorage.getItem('userId')}
  })
}

export function getMatchingDemand (id) {
  return axios({
    url: 'api/needs/getneeds?id='+id,
    method: 'get',
  })
}

export function getDemandList (id) {
  return axios({
    url: 'api/needs/getneeds?id='+id,
    method: 'get',
  })
}

export function getDemandDetailById (id) {
  return axios({
    url: 'api/needs/getneedinfo?id='+id,
    method: 'get',
  })
}

export function getOrderDetailById (id) {
  return axios({
    url: 'api/order/getOrders?id='+id,
    method: 'get',
  })
}

export function payForOrders (parameter) {
  return axios({
    url: 'api/order/payForOrders',
    method: 'post',
    data: parameter
  })
}

export function postBasicInfoRequest (parameter) {
  return axios({
    url: 'api/user/entBasicinfoPost',
    method: 'post',
    data: parameter
  })
}

export function postEntQualificationRequest (parameter) {
  return axios({
    url: 'api/user/entNamePost',
    method: 'post',
    data: parameter
  })
}



